import React from 'react';
import Typography from '@material-ui/core/Typography';
import { Grid, Paper, IconButton } from '@material-ui/core';
import AddDocumentStyles from '../styles/AddDocument';
import { makeStyles } from '@material-ui/styles';
import DocumentInformations from './DocumentInformations'
import Title from './Title';
import { useParams } from "react-router";
import { useSnackbar } from 'notistack';
import { CircularProgress, Fade } from '@material-ui/core';
import Axios from 'axios';
import config from '../config.json';
import { saveAs } from 'file-saver';
import Link from '@material-ui/core/Link';
import TextField from '@material-ui/core/TextField';
import Button from '@material-ui/core/Button'
import CloseIcon from '@material-ui/icons/Close';

const useStyles = makeStyles(AddDocumentStyles);



export default function DocumentView(props) {
  const {
    handleShare,
    document,
    setDocument,
    handleDownload,
    web3Manager,
    setLoading } = props;

  React.useEffect(() => {
    handleLoadNotary();
  }, []);

  const classes = useStyles();


  const [error, setError] = React.useState(undefined)
  const [publicKey, setPublicKey] = React.useState('');
  const snackbar = useSnackbar();
  const [submitInProgress, setSubmitInProgress] = React.useState(false);

  const handleLoadNotary = async () => {
    try {
      setSubmitInProgress(true);
      setLoading(true);
      const documentObj = document;
      const isNotary = await web3Manager.notaryManagerContract.methods.isNotary(document.notaryPublishedBy).call();
      if (!isNotary) {
        return snackbar.enqueueSnackbar('Podany klucz publiczny nie naley do adnego notariusza w tym systemie.', { variant: 'error' });
      }
      const result = await web3Manager.notaryManagerContract.methods.notaries(document.notaryPublishedBy).call();
      const notaryContractCloned = web3Manager.notaryContract.clone();
      notaryContractCloned.options.address = result;
      documentObj["notaryName"] = await notaryContractCloned.methods.name().call();
      documentObj["notarySurname"] = await notaryContractCloned.methods.surname().call();
      documentObj["notaryOfficeLocation"] = await notaryContractCloned.methods.officeLocation().call();  

      setDocument(documentObj)
    } catch (err) {
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si wczyta danych notariusza: ' + err.message, { variant: 'error' });
    } finally {
      setSubmitInProgress(false);
      setLoading(false);
    }
  }

  const handleChange = event => {
    const inputName = event.target.name;
    if (inputName === 'publicKey') {
      setPublicKey(event.target.value);
    }
  };

  return (

    <Paper className={classes.paper} >
      <Grid container spacing={3}>
        <Grid item xs={12}>

        <Grid container>
          <Grid item style={{flexGrow: 1}}>

          <Title>
            Szczegy dokumentu
          </Title>
          </Grid>
          <Grid item>
            <IconButton onClick={() => { 
              setDocument(undefined);
            }}> 
              <CloseIcon />    
            </IconButton>
          </Grid>
        </Grid>

          <Title>
            </Title>
          <Typography variant="body1">
            W tym widoku znajdziesz szczegowe informacje o dokumencie.
            </Typography>



          {error === undefined ?
            <React.Fragment>

              <DocumentInformations document={document} />


              <Grid container alignItems="center" spacing={4} >
                <Grid item xs={8}> 
                  <TextField
                    id="publicKey"
                    name="publicKey"
                    label="Klucz Publiczny osoby ktrej udostpniamy dokument"
                    className={classes.textField}
                    margin="none"
                    onChange={handleChange}
                    value={publicKey}
                  />
                </Grid>
                <Grid item xs={4}>
                  <Button variant="contained" color="primary" style={{width:'100%'}} onClick={e => handleShare(document.documentAddress, publicKey)}>
                    Udostpnij
                  </Button> 
                </Grid> 
              </Grid>
              <Grid container>
                <Grid item xs={12}>
                  <Button variant="contained" color="primary" style={{width:'100%'}} onClick={e => handleDownload(document.link, document.documentAddress, document.name)}>
                    Pobierz
                  </Button>
                </Grid>
              </Grid>

            </React.Fragment>
            :
            <Typography style={{ margin: '10px 0' }} variant="body1">
              Bd: {error.msg}
            </Typography>
          }
 
        </Grid>
      </Grid >
    </Paper>
  );
}
