import React from 'react'; 
import Typography from '@material-ui/core/Typography';
import { Grid, Paper } from '@material-ui/core';
import AddDocumentStyles from '../styles/AddDocument';
import { makeStyles } from '@material-ui/styles';
import AddDocumentForm from '../views/AddDocumentForm'
import Title from '../views/Title';

const useStyles = makeStyles(AddDocumentStyles);

export default function AddDocument(props) {
  const classes = useStyles();
  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Paper className={classes.paper}>
          <Title>
            Dodaj dokument
          </Title>
          <Typography variant="body1">
            Na tej stronie jako notariusz mozesz doda dokument.
          </Typography> 

        </Paper>
      </Grid>
      <AddDocumentForm web3Manager={props.web3Manager} setLoading={props.setLoading}/> 
    </Grid>
  );
}
 