/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { useSnackbar } from 'notistack';
import Axios from 'axios';
import config from '../config.json';
import { saveAs } from 'file-saver';
import { Exception } from 'handlebars';
import DocumentView from '../views/DocumentView'
import FileTable from '../views/FileTableView'
import FileExplorerStyles from '../styles/FileExplorer';
import EthCrypto from 'eth-crypto';
import notifyOnMiningStatus from '../helpers/miningStatusNotifier.js';
import encryption from '../helpers/encryption.js';
import toBuffer from 'blob-to-buffer';

// Generate Order Data
function createData(name, shortDescription, date, link, documentAddress, notaryPublishedBy, timestamp) {
  return { name, shortDescription, date, link, documentAddress, notaryPublishedBy, timestamp};
}

const useStyles = makeStyles(FileExplorerStyles);
const pageSize = 10;

export default function FileExplorer(props) {
  const { setLoading, web3Manager, selectedAddress} = props;


  const snackbar = useSnackbar();
  const [selectedDocument, setDocument] = React.useState(undefined);
  const [submitInProgress, setSubmitInProgress] = React.useState(false);
  const [fetchingDocuments, setFetchingDocuments] = React.useState(false);
  const [componentMounted, setComponentMounted] = React.useState(false); 
  const [uniqueDocuments, setUniqueDocuments] = React.useState([]); 
  const [rows, setRows] = React.useState([]); 

  const refreshDocuments = () => {
    setRows([]);
    updateUniqueDocuments();
  }
  React.useEffect(() => { 
    refreshDocuments();
  }, [selectedAddress]);

  React.useEffect(() => {
    if (uniqueDocuments.length > 0) {
      getFilesData();
    }
  }, [uniqueDocuments]);

  const loadDocuments = async (startingIndex, count) => {
    if (startingIndex >= uniqueDocuments.length) {
      throw new Exception("Brak dokumentw do zaadowania.");
    }

    const fetchAllDocuments = uniqueDocuments.slice(startingIndex, startingIndex + count).map((documentAddress) => {
      return new Promise(async (resolve, reject) => {
        try {
          const documentContractCloned = web3Manager.documentContract.clone();
          documentContractCloned.options.address = documentAddress;

          let title = await documentContractCloned.methods.title().call();
          let ipfsHash = await documentContractCloned.methods.ipfsHash().call();
          let createdAt = await documentContractCloned.methods.createdAt().call();
          let notaryPublishedBy = await documentContractCloned.methods.notaryPublishedBy().call();
          var date = new Date(createdAt * 1000);
          return resolve(createData(title, "Umowa", date.toLocaleString(), ipfsHash, documentAddress, notaryPublishedBy, createdAt));
        } catch (err) {
          return reject(err);
        }
      })

    })
    const allDocuments = await Promise.all(fetchAllDocuments);
    console.log('rows', allDocuments);
    return allDocuments;
  }

  const getFilesData = async () => {
    console.log('Fetching documents allowed for address:', selectedAddress)
    try {
      setFetchingDocuments(true);
      setLoading(true);
      const pages = Math.ceil(uniqueDocuments.length / pageSize);
      let currentPage = 0;
      const totalDocuments = [];
      while(currentPage < pages){
        
        setLoading(true);
        try{ 
          const documents = await loadDocuments(totalDocuments.length, pageSize);
          console.log('documents', documents);
          totalDocuments.push(...documents);
          setRows([...totalDocuments]);
          currentPage++;
        } catch (err) {
          console.error(err);
        }
      }
    } catch (err) {
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si odczyta dokumentw: ' + err.message, { variant: 'error' });
    } finally {
      setLoading(false);
      setFetchingDocuments(false);
    }
  }

  const updateUniqueDocuments = async () => {
    try {
      setLoading(true);

      const result = await web3Manager.documentManagerContract.methods.getDocuments(selectedAddress).call();
      console.log('documents for address', selectedAddress, result);
      const uniqueDocumentsFromBlockchain = [...new Set(result)].reverse();
      setUniqueDocuments(uniqueDocumentsFromBlockchain);
    } catch (err) {
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si wczyta dokumentw do ktrych masz dostp: ' + err.message, { variant: 'error' });
    } finally {
      setLoading(false);
      setComponentMounted(true);
    }
  }

  const handleShare = async (documentAddress, targetPublicKey) => {
    try {
      if(!web3Manager.isValidPublicKey(targetPublicKey)){
        throw new Error("Nieprawidowy klucz publiczny do udostpnienia.")
      }
      setSubmitInProgress(true);
      setLoading(true);
      console.log("Share document ", documentAddress, " to ", targetPublicKey);
      const documentContractCloned = web3Manager.documentContract.clone();
      documentContractCloned.options.address = documentAddress;
      // deszyfrujemy i szyfrujemy ...

      
      let accountEncryptedKeySimplified = await documentContractCloned.methods.decryptionKeys(web3Manager.selectedAddress).call();
      console.log('not encrypted', accountEncryptedKeySimplified)
      //accountEncryptedKeySimplified = "";
      const accountEncryptedKey = EthCrypto.cipher.parse(accountEncryptedKeySimplified);
      console.log("accountEncryptedKey", accountEncryptedKey);
      const accountDecryptedKey = await EthCrypto.decryptWithPrivateKey(
        web3Manager.getPrivateKeyForSelectedAddress(),
        accountEncryptedKey
      );
      console.log("acountDecryptedKey", accountDecryptedKey);
      const toShareEncryptedKey = await EthCrypto.encryptWithPublicKey(
        targetPublicKey,
        accountDecryptedKey
      );
      console.log("encrypted", toShareEncryptedKey);
      console.log("encrypted Siplified", EthCrypto.cipher.stringify(toShareEncryptedKey))
      const targetAddress = EthCrypto.publicKey.toAddress(targetPublicKey);

      const deploymentPromiEvent = web3Manager.addPermission(targetAddress, EthCrypto.cipher.stringify(toShareEncryptedKey), documentAddress);
      console.log("deploymentPromiEvent");
      const miningStatus = await notifyOnMiningStatus(deploymentPromiEvent, snackbar, 'Udostpnianie dokumentu');
      console.log('mining status', miningStatus); 
      return true;
    } catch (err) {
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si udostpni dokumentu: ' + err.message, { variant: 'error' });
    } finally {
      setLoading(false);
      setSubmitInProgress(false);
    }
    return false;
  }

  const handleDownload = async (ipfsHash, documentAddress, title) => { 
    try {
      setSubmitInProgress(true);
      setLoading(true);

      const documentContractCloned = web3Manager.documentContract.clone();
      documentContractCloned.options.address = documentAddress;
      let keySimplified = await documentContractCloned.methods.decryptionKeys(web3Manager.selectedAddress).call();
      const key = EthCrypto.cipher.parse(keySimplified); 
      const decryptedKey = await EthCrypto.decryptWithPrivateKey(
        web3Manager.getPrivateKeyForSelectedAddress(),
        key
      ); 
      let encryptedDocument
      if(!config.includeNotaryPanel){
        const axiosInstance = Axios.create({withCredentials: false});
        encryptedDocument = await axiosInstance.get(`${config.publicIpfsGateway}/${ipfsHash}`, { responseType: 'blob', timeout: config.ipfsDownloadTimeout});
      } else {
        encryptedDocument = await Axios.get(`${config.ipfsGateway}/${ipfsHash}`, { responseType: 'blob', timeout: config.ipfsDownloadTimeout}); 
      }
      const contentArrayBuffer = await new Promise((resolve, reject) => {
        try{ 
          toBuffer(encryptedDocument.data, (err, buffer) => {
            if (err) throw err
            return resolve(buffer);
          });
        }catch(err){
          return reject(err);
        }
      })  
      const decryptedContent = encryption.decrypt(contentArrayBuffer, decryptedKey);

      const blob = new Blob([decryptedContent], {
        type: 'application/pdf',
      }); 
      saveAs(blob, title + ".pdf");
      return true;
    } catch (err) {
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si pobra dokumentu: ' + err.message, { variant: 'error' });
    } finally {
      setLoading(false);
      setSubmitInProgress(false);
    } 
    return false;
  }
  
 


  return (
    <React.Fragment>

      {!selectedDocument ?
        <FileTable {...props}
          refreshDocuments={refreshDocuments}
          fileExplorerComponentMounted={componentMounted} 
          setDocument={setDocument}
          fetchingDocuments={fetchingDocuments} 
          rows={rows}
          handleShare={handleShare}
          handleDownload={handleDownload} 
          web3Manager={web3Manager}
        />
        :
        <DocumentView {...props}
          document={selectedDocument}
          setDocument={setDocument}
          handleShare={handleShare} 
          handleDownload={handleDownload}
          web3Manager={web3Manager}
          setLoading={setLoading}
        />
      }</React.Fragment>
  );
}
