/* eslint-disable react-hooks/exhaustive-deps */
import React, { Fragment } from 'react';  
import { Grid, Paper, Fade, Typography, CircularProgress, Button, TextField } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import dashboardStyles from '../styles/Dashboard'; 
import web3Manager from '../helpers/web3Manager';
import { useSnackbar } from 'notistack';

const useStyles = makeStyles(dashboardStyles);

export default function Web3Provider(props) {
  const {setLoading, setNotaryManager, setNotary, setAccountSelected} = props;//
  const classes = useStyles();  
  const snackbar = useSnackbar();

  const [mnemonic, setMnemonic] = React.useState("");
  const [web3Initialised, setWeb3Initialized] = React.useState(false);
  const [web3FailedInitialization, setWeb3FailedInitialization] = React.useState(false);

  const [selectedAddress, setSelectedAddress] = React.useState(null);


  const updateAccountSelected = async (address) => {
    console.log("Update current selected address:", address);
    setSelectedAddress(address);
    setNotary(await web3Manager.isNotary());
    setNotaryManager(await web3Manager.isOwner());
  } 
  
  const handleChange = event => {
    const inputName = event.target.name;
    if(inputName === 'mnemonic'){
      setMnemonic(event.target.value);
    } 
  };

  
  // const waitForWeb3 =  async () => {
  //   setLoading(true);
  //   setWeb3FailedInitialization(false);
  //   try{
  //     await web3Manager.initializing;
  //     setWeb3Initialized(true);

  //     await updateAccountSelected(web3Manager.selectedAddress);
      
  //     setLoading(false);
  //   }catch(err){
  //     console.error(err);
  //     setWeb3FailedInitialization(true);
  //     snackbar.enqueueSnackbar("Nie udao si poczy z Web3. " + err.message, {variant:'error'});
  //     setLoading(false);
  //   } 
  // };

  // React.useEffect(() => {
  //   web3Manager.onAccountChange(updateAccountSelected);
  //   waitForWeb3()
  // }, []); 

  const handleLogin = async () => {
    try{
      if(mnemonic.length === 0){
        return snackbar.enqueueSnackbar("Musisz podac mnemonic wygenerowany przy pierwszym logowaniu", {variant:'error'});
      }
      await web3Manager.initWeb3Manager(mnemonic);
      await updateAccountSelected(web3Manager.selectedAddress);
      setWeb3Initialized(true);
      setAccountSelected(web3Manager.selectedAddress);
    }catch(err){
      console.error(err);
      snackbar.enqueueSnackbar("Nie udao si zalogwoac " + err.message, {variant:'error'});
    }
    
  }

  const handleCreateAccount = async () => {
    const newMnemonic = web3Manager.generateMnemonic();
    setMnemonic(newMnemonic);
    snackbar.enqueueSnackbar(
      <Grid container direction="column"> 
        <Typography variant="body1" gutterBottom>
          Wygenerowano nowe konto. Pamitaj aby <b>zapisa</b> mnemonic. Bez niego nie bdziesz mg si zalogowa na swoje konto w przyszoci.
        </Typography>
        <Typography variant="overline">
          {newMnemonic}
        </Typography>
      </Grid>, {variant:'success'});
  }

  if(web3Initialised){ 
    return props.render({
      ...props,
      selectedAddress,
      web3Manager 
    });
  } 
  return (
    <Fade in timeout={1000}> 
      <Paper className={classes.paper}>
        <Grid container>
            <Grid item xs={12}>
              <Typography>
                Zaloguj si podajac Mnemonic, ktory wygenerowales podczas pierwszego uzycia aplikacji:
              </Typography>
              <TextField
                id="mnemonic"
                name="mnemonic"
                label="Mnemonic (12 slow)"
                className={classes.textField} 
                margin="normal"
                onChange={handleChange}
                value={mnemonic}
                inputProps={{autoComplete: "off"}}
              />
            </Grid>
            <Grid item xs={12}>
              <Button variant="contained" color="primary" onClick={handleLogin} className={classes.actionButton}>
                Zaloguj
              </Button>
              <Button variant="contained" onClick={handleCreateAccount} className={classes.actionButton}>
                Utworz nowe konto
              </Button>
            </Grid>
            {web3FailedInitialization && 
              <Button variant="contained" >
                Ponw prb
              </Button>
            }
        </Grid>
      </Paper>
    </Fade> 
  )
}
 