import Web3 from 'web3';
import HDWalletProvider from "@truffle/hdwallet-provider";
import NotaryManager from '../smartContracts/NotaryManager.json'; 
import DocumentManager from '../smartContracts/DocumentManager.json'; 
import Document from '../smartContracts/Document.json'; 
import Notary from '../smartContracts/Notary.json'; 
import config from '../config.json'; 
import EthCrypto from 'eth-crypto';
const bip39 = require('bip39'); 

class Web3Manager {
  constructor(){
    window.web3Manger = this;
    this.web3 = null;
    
    this.notaryManagerContract = null;
    this.documentManagerContract = null;
    this.notaryContract = null;
    this.documentContract = null;

    this.selectedAddress = null;
    this.accountChangeListeners = [];
  }
 
  fromWei(wei, unit){
    return this.web3.utils.fromWei(wei, unit);
  }

  isValidAddress(address){
    return this.web3.utils.isAddress(address);
  }
  isValidPublicKey(pubKey){
    try{
      const address = EthCrypto.publicKey.toAddress(pubKey);
      return this.isValidAddress(address);
    }catch(err){
      return false;
    } 
  }


  async updateSelectedAccount(accountInfo){
    if(accountInfo.selectedAddress === this.selectedAddress){
      return;
    }
    this.selectedAddress = accountInfo.selectedAddress; 
    this.accountChangeListeners.forEach((callback) => callback(this.selectedAddress));
  }

  getPublicKeyForSelectedAddress(){ 
    const wallet = this.web3.currentProvider.wallets[this.selectedAddress];
    return wallet.pubKey.toString("hex")
  }

  getPrivateKeyForSelectedAddress(){ 
    const wallet = this.web3.currentProvider.wallets[this.selectedAddress];
    return wallet.privKey.toString("hex")
  }

  async getCurrentBalance(){
    return this.web3.eth.getBalance(this.selectedAddress);
  }
  onAccountChange(callback){
    this.accountChangeListeners.push(callback);
  }
 
  generateMnemonic(){
    // const bip39 = require('bip39');
    console.log("bip", bip39)
    return bip39.generateMnemonic()
  }

  async initWeb3Manager(mnemonic){  
    console.log('Using infura as web3 provider.', mnemonic);
    this.web3 = new Web3(
      new HDWalletProvider(mnemonic, 'https://ropsten.infura.io/v3/2110b3b8a5974c83a3bff2c3a4cb0527')
    );
       
    window.web3 = this.web3; 
    this.selectedAddress = this.web3.currentProvider.addresses[0];

    
    this.notaryManagerContract = new this.web3.eth.Contract(NotaryManager.abi, config.notaryManagerAddress);
    this.documentManagerContract = new this.web3.eth.Contract(DocumentManager.abi, config.documentManagerAddress);
    this.documentContract = new this.web3.eth.Contract(Document.abi);
    this.notaryContract = new this.web3.eth.Contract(Notary.abi); 
  }

  async isNotary() {
    return await this.notaryManagerContract.methods.isNotary(this.selectedAddress).call(); 
  }

  async isOwner() {
    return await this.notaryManagerContract.methods.isOwner().call({from: this.selectedAddress}); 
  }
  
  /**
   * 
   * @param {String} name 
   * @param {String} surname 
   * @param {Address} publicKey 
   * @param {String} officeLocation 
   * @returns {PromiEvent} promiEvent
   */
  deployNotary(name, surname, publicKey, officeLocation){
    return this.notaryContract.deploy({
        data: Notary.bytecode,
        arguments: [name, surname, publicKey, officeLocation]
      }).send({
        from: this.selectedAddress
      });
  }
  addNotary(contractAddress){
    return this.notaryManagerContract.methods.addNotary(contractAddress).send({
      from: this.selectedAddress
    });
  }
  updateNotary(contractAddress, method, value){
    const notaryContractCloned = this.notaryContract.clone();
    notaryContractCloned.options.address = contractAddress; 
    return notaryContractCloned.methods[method](value).send({
      from: this.selectedAddress
    }); 
  }
  removeNotary(notaryPublicKey){
    return this.notaryManagerContract.methods.removeNotary(notaryPublicKey).send({
      from: this.selectedAddress
    });
  }
  addDocument(title, ipfsHash, myEncryptedKey){
    return this.documentManagerContract.methods.addDocument(title, ipfsHash, myEncryptedKey ).send({
      from: this.selectedAddress
    });
  }
  getDocuments(address){
    return this.documentManagerContract.methods.getDocuments(address).call()
  }
  addPermission(account, accountEncryptedKey, document){
    return this.documentManagerContract.methods.addPermission(account, accountEncryptedKey, document).send({
      from: this.selectedAddress
    });
  }

  async listAllNotaries(){
    
    // this.notaryManagerContract.NotaryStatusChange({}, { fromBlock: 0, toBlock: 'latest' }).get((error, eventResult) => {
    //   if (error)
    //     console.log('Error in myEvent event handler: ' + error);
    //   else
    //     console.log('myEvent: ' + JSON.stringify(eventResult.args));
    // });
    const notaryStatusChangeRawEvents = await this.notaryManagerContract.getPastEvents("NotaryStatusChange", {fromBlock: 0});
    const notaryStatusChangeEvents = notaryStatusChangeRawEvents.map((event) => ({notaryPublicKey: event.returnValues[0], status: event.returnValues[1], block: event.blockNumber}))
    const notariesStatuses = {};
    notaryStatusChangeEvents.forEach((event) => {
      notariesStatuses[event.notaryPublicKey] = event;
    })
    const listOfAllNotaries = Object.keys(notariesStatuses).map((notaryStatus) => ({
      notaryPublicKey: notariesStatuses[notaryStatus].notaryPublicKey, 
      block: notariesStatuses[notaryStatus].block, 
      status: notariesStatuses[notaryStatus].status === "0" ? "Aktywny" : "Zablokowany"
    }))
    
    return listOfAllNotaries;
  }

} 
export default new Web3Manager();
 