/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import Link from '@material-ui/core/Link';
import { makeStyles } from '@material-ui/core/styles'; 
import TextField from '@material-ui/core/TextField';
import Grid from '@material-ui/core/Grid';
import GetAppIcon from '@material-ui/icons/GetApp';
import ShareIcon from '@material-ui/icons/Share';
import RefreshIcon from '@material-ui/icons/Refresh';
import tableIcons from './tableIcons';

import Title from './Title';
import { CircularProgress, Fade, Typography, Paper, Button, IconButton } from '@material-ui/core'; 
import MaterialTable from 'material-table';
import { useSnackbar } from 'notistack';


 
const useStyles = makeStyles(theme => ({
  seeMore: {
    marginTop: theme.spacing(3),
  },
  link: {
    padding: "0 0.5rem",
    cursor: "pointer"
  },
  textField: {
    width: '50%',
    margin: '0',
  },
  fetchingDataFromBlockchain: {
    marginTop: theme.spacing(3)
  },
  paper:{
    padding: theme.spacing(4),
    marginBottom: theme.spacing(1),
    display: 'flex',
    overflow: 'auto',
    flexDirection: 'column'
  }
}));


export default function FileTableView(props) {
  const {
    setDocument,
    fetchingDocuments,
    rows,
    handleShare,
    handleDownload,
    fileExplorerComponentMounted,
    web3Manager,
    refreshDocuments} = props;

  const classes = useStyles(); 
  const snackbar = useSnackbar();
 
  const [publicKey, setPublicKey] = React.useState('');
  const handleChange = event => {
    const inputName = event.target.name;
    if (inputName === 'publicKey') {
      setPublicKey(event.target.value);
    }
  };

  if(!fileExplorerComponentMounted){ 
    return (

      <Fade in timeout={1000}>
        <Paper className={classes.paper}>
          <Grid container justify="center" alignItems="center" direction="column">
            <Typography gutterBottom className={classes.connectServerText}>
              Wczytywanie dokumentw...
              </Typography>
            <CircularProgress />
          </Grid>
        </Paper>
      </Fade> 
    );
  }

  const handleDownloadDocuments = async (context, documents) => {
    console.log("download documents", documents); 
    snackbar.enqueueSnackbar(`Zostanie pobranych ${documents.length} dokumentw.`, { variant: 'info' });
    const downloadDocumentsPromises = documents.map((doc) => new Promise((resolve, reject) => { 
      handleDownload(doc.link, doc.documentAddress, doc.name).then(resolve).catch(reject);
    }))
    try{ 
      const downloadResult = await Promise.all(downloadDocumentsPromises);
      const failedDocs = downloadResult.filter((elem) => elem === false);
      if(failedDocs.length > 0){  
        snackbar.enqueueSnackbar('Nie udao si pobra '+failedDocs.length+' dokumentw', { variant: 'error' });
      } else{
        snackbar.enqueueSnackbar(`Pomylnie pobrano ${documents.length} dokumentw.`, { variant: 'success' });
      }  
    }catch(err){ 
      console.error(err); 
    }
  }
  const handleShareDocuments = async (context, documents) => {
    console.log("share documents", documents); 
    try{ 
      if(!web3Manager.isValidPublicKey(publicKey)){
        throw new Error("Nieprawidowy klucz publiczny do udostpnienia.")
      }
      snackbar.enqueueSnackbar(`Zostanie udostpnionych ${documents.length} dokumentw.`, { variant: 'info' });
  
      const shareDocumentsPromises = documents.map((doc) => new Promise((resolve, reject) => { 
        handleShare(doc.documentAddress, publicKey).then(resolve).catch(reject);
      }))
      const shareResult = await Promise.all(shareDocumentsPromises);
      const failedDocs = shareResult.filter((elem) => elem === false);
      if(failedDocs.length > 0){  
        snackbar.enqueueSnackbar('Nie udao si udostpni '+failedDocs.length+' dokumentw', { variant: 'error' });
      } else{

        snackbar.enqueueSnackbar(`Pomylnie udostpniono ${documents.length} dokumentw.`, { variant: 'success' });
      } 
    }catch(err){ 
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si udostpni dokumentw: ' + err.message, { variant: 'error' });
    }

  }

  return (
        <React.Fragment> 
          
          <Paper className={classes.paper} > 
            <Grid item xs={12}>
              <Title>
                Dane do udostpnienia
              </Title>
              <TextField
                style={{width:'100%'}}
                id="publicKey"
                name="publicKey"
                label="Klucz Publiczny osoby ktrej udostpniamy dokument"
                className={classes.textField}
                margin="none"
                onChange={handleChange}
                value={publicKey}
              />

            </Grid>
          </Paper>

          <MaterialTable
              columns={[
                { title: "Nazwa Pliku", field: "name", render: (row) => { 
                  return (
                    <Link color="primary" className={classes.link} onClick={ e => {
                      e.preventDefault();
                      setDocument(row)
                    }}>
                      {row.name}
                    </Link>
                  ) 
                } },
                { title: "Data podpisania", field: "date", defaultSort: "desc", customSort: (a, b) => a.timestamp - b.timestamp }
              ]}
              data={rows}
              icons={tableIcons}
              title={(
                <Grid container alignItems="center">
                  <Grid item>
                    <Title>
                      Dokumenty
                    </Title>
                  </Grid>
                  <Grid item style={{marginTop:'-2px'}}>
                    <IconButton onClick={refreshDocuments}>
                      <RefreshIcon/>
                    </IconButton>
                  </Grid>
                </Grid>
              )}
              actions={[
                {
                  icon: () => <ShareIcon/>,
                  tooltip: 'Udostpnij',
                  onClick: handleShareDocuments
                },
                {
                  icon: () => <GetAppIcon/>,
                  tooltip: 'Pobierz',
                  onClick: handleDownloadDocuments
                }
              ]} 
              // onSelectionChange={handleSelectionChange}
              options={{
                filtering: false,
                sorting: true,
                grouping: false,
                draggable: false,
                paging: true,
                pageSize: 5,
                pageSizeOptions: [5, 10, 25, 50, 100],
                selection: true,
                actionsColumnIndex: -1
              }} 
              localization={{
                body:{
                  emptyDataSourceMessage: fetchingDocuments ? "Wczytywanie dokumentw...":"Brak dokumentw."
                }
              }}
            />

          
          
        </React.Fragment>
        
  );
}
