import React from 'react'; 
import web3Manager from '../helpers/web3Manager';
import { Grid, Typography, Dialog, DialogTitle, DialogContent, IconButton, Tooltip, Table, TableHead, TableRow, TableCell, TableBody } from '@material-ui/core';
import AccountCircle from '@material-ui/icons/AccountCircle';

export default function AccountDataView(props) {
  
  const [open, setOpen] = React.useState(false);
  const [curentBalanceText, setCurentBalanceText] = React.useState("Wczytywanie...");
  const handleOpen =() => {
    setCurentBalanceText("Wczytywanie...");
    web3Manager.getCurrentBalance()
      .then((value) => setCurentBalanceText(web3Manager.fromWei(value, 'ether') + ' ETH'))
      .catch((err) => {
        console.error(err);
        setCurentBalanceText("Blad wczytywania.");
      });

    setOpen(true);
    
  }

  const handleClose = () => { 
    setOpen(false);
  }
  return (
    <div> 
      <Tooltip title="Wyswietl profil">
        <IconButton
          color="inherit"
          onClick={handleOpen}
          >
          <AccountCircle />
        </IconButton>
      </Tooltip>
      <Dialog onClose={handleClose} open={open}  maxWidth={'xl'}>
        <DialogTitle>Informacje o uzytkowniku</DialogTitle>
        <DialogContent>
          <Grid container style={{display:'block'}}>
            <Grid item>
              <Typography variant="body1">
                To okno prezentuje dane o twoim koncie niezbdne do poprawnego uytkowania aplikacji.
              </Typography>
            </Grid>
            <Grid item>
              <Table size="small">
                <TableHead>
                  <TableRow>
                    <TableCell style={{ width: '30%' }}>Parametr</TableCell>
                    <TableCell>Warto</TableCell> 
                  </TableRow>
                </TableHead>
                <TableBody> 
                <TableRow >                      
                    <TableCell>
                      <Typography>
                        <b>
                          Twoj Klucz Publiczny
                        </b>
                      </Typography>
                    </TableCell>
                    <TableCell >
                      {web3Manager.getPublicKeyForSelectedAddress()}  
                    </TableCell>
                  </TableRow> 

                  <TableRow >                      
                    <TableCell>Address Konta</TableCell>
                    <TableCell >
                      {web3Manager.selectedAddress}
                    </TableCell>
                  </TableRow>  

                  <TableRow >                      
                    <TableCell>Srodki na koncie</TableCell>
                    <TableCell >
                      {curentBalanceText}
                    </TableCell>
                  </TableRow>  
                  
                </TableBody>
              </Table> 
            </Grid>
          </Grid> 
        </DialogContent>
      </Dialog>
    </div>
  );
}
 