import React from 'react'; 
import Typography from '@material-ui/core/Typography';
import { Grid, Paper } from '@material-ui/core';
import notaryManagerStyles from '../styles/NotaryManager';
import { makeStyles } from '@material-ui/styles';
import Title from '../views/Title';
import AddNotaryForm from '../views/AddNotaryForm';
import EditNotaryForm from '../views/EditNotaryForm';
import DeleteNotaryForm from '../views/DeleteNotaryForm';
import ListAllNotaries from '../views/ListAllNotaries';

const useStyles = makeStyles(notaryManagerStyles);

export default function NotaryManager(props) {
  const classes = useStyles();
  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Paper className={classes.paper}>
          <Title>
            Panel zarzdzania notariuszami
          </Title>
          <Typography variant="body1">
            Jako administrator masz dostp do panelu zarzdzania notariuszami. W tym panelu moesz dodawa i usuwa notariuszy. Tylko autoryzowani notariusze mog dodawa dokumenty do systemu.
          </Typography> 

        </Paper>
      </Grid>
      <ListAllNotaries web3Manager={props.web3Manager} setLoading={props.setLoading}/>
      <AddNotaryForm web3Manager={props.web3Manager} setLoading={props.setLoading} />
      <EditNotaryForm web3Manager={props.web3Manager} setLoading={props.setLoading} />
      <DeleteNotaryForm web3Manager={props.web3Manager} setLoading={props.setLoading} />
    </Grid>
  );
}
 