import React from 'react'; 
import Typography from '@material-ui/core/Typography';
import { Grid, Paper, TextField, Button, IconButton } from '@material-ui/core';
import notaryManagerStyles from '../styles/NotaryManager';
import { makeStyles } from '@material-ui/styles';
import SaveIcon from '@material-ui/icons/Save';
import Title from './Title';
import { useSnackbar } from 'notistack';
import notifyOnMiningStatus from '../helpers/miningStatusNotifier';

const useStyles = makeStyles(notaryManagerStyles);

export default function DeleteNotaryForm(props) { 
  const {web3Manager, setLoading} = props; 
  const classes = useStyles(); 
  const snackbar = useSnackbar();

  const [submitInProgress, setSubmitInProgress] = React.useState(false);
  const [notaryPublicKey, setNotaryPublicKey] = React.useState('');
  const [notaryAccountAddress, setNotaryAccountAddress] = React.useState(''); 
  const [name, setName] = React.useState('');
  const [surname, setSurname] = React.useState(''); 
  const [officeLocation, setOfficeLocation] = React.useState('');
  const handleChange = event => {
    const inputName = event.target.name;
    if(inputName === 'notaryPublicKey'){
      setNotaryPublicKey(event.target.value);
    } 
    if(inputName === 'name'){
      setName(event.target.value);
    }
    if(inputName === 'surname'){
      setSurname(event.target.value);
    }
    if(inputName === 'officeLocation'){
      setOfficeLocation(event.target.value);
    }
  };

  const handleRemoveNotary = async () => {
    try {
      setSubmitInProgress(true);
      setLoading(true);
      const isNotary = await web3Manager.notaryManagerContract.methods.isNotary(notaryPublicKey).call();
      if(!isNotary){
        return snackbar.enqueueSnackbar('Podany klucz publiczny nie naley do adnego notariusza w tym systemie.', {variant: 'error'});
      }

      const removeNotaryPromiEvent = web3Manager.removeNotary(notaryPublicKey); 
      const miningStatus = await notifyOnMiningStatus(removeNotaryPromiEvent, snackbar, 'Usuwanie notariusza'); 
      console.log('mining status' , miningStatus); 
 
    } catch (err) { 
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si usun uprawnie notariusza: ' + err.message, {variant: 'error'});
    } finally {
      setSubmitInProgress(false);
      setLoading(false);
    }
  }
 
  return (
    <Grid item xs={6}>
      <Paper className={classes.paper}>
        <Title>
          Usu notariusza
        </Title>
        <Typography variant="body1">
          Aby odebra uprawnienia do systemu dla notariusza, naley umieci w polu poniej jego adres publiczny, a nastpnie zapisa zmiany na blockchainie.
        </Typography>  
        <div className={classes.textFieldRoot}> 
          <TextField
            id="notaryPublicKey"
            name="notaryPublicKey"
            label="Adres (kryptowalutowy) Notariusza"
            className={classes.textField} 
            margin="normal"
            onChange={handleChange}
            value={notaryPublicKey} 
          />
        </div> 
        <Button variant="contained" color="secondary" onClick={handleRemoveNotary} disabled={submitInProgress}>
          Usu uprawnienia
        </Button>
         
      </Paper>
    </Grid>
  );
}
 