import React from 'react'; 
import Typography from '@material-ui/core/Typography';
import { Grid, Paper, TextField, Button, IconButton } from '@material-ui/core';
import notaryManagerStyles from '../styles/NotaryManager';
import { makeStyles } from '@material-ui/styles';
import SaveIcon from '@material-ui/icons/Save';
import Title from './Title';
import { useSnackbar } from 'notistack';
import notifyOnMiningStatus from '../helpers/miningStatusNotifier';

const useStyles = makeStyles(notaryManagerStyles);

export default function EditNotaryForm(props) { 
  const {web3Manager, setLoading} = props; 
  const classes = useStyles(); 
  const snackbar = useSnackbar();

  const [submitInProgress, setSubmitInProgress] = React.useState(false);
  const [notaryPublicKey, setNotaryPublicKey] = React.useState('');
  const [notaryAccountAddress, setNotaryAccountAddress] = React.useState(''); 
  const [name, setName] = React.useState('');
  const [surname, setSurname] = React.useState(''); 
  const [officeLocation, setOfficeLocation] = React.useState('');
  const handleChange = event => {
    const inputName = event.target.name;
    if(inputName === 'notaryPublicKey'){
      setNotaryPublicKey(event.target.value);
    } 
    if(inputName === 'name'){
      setName(event.target.value);
    }
    if(inputName === 'surname'){
      setSurname(event.target.value);
    }
    if(inputName === 'officeLocation'){
      setOfficeLocation(event.target.value);
    }
  };

  const handleLoadNotary = async () => {
    try {
      setSubmitInProgress(true);
      setLoading(true);
      const isNotary = await web3Manager.notaryManagerContract.methods.isNotary(notaryPublicKey).call();
      if(!isNotary){
        return snackbar.enqueueSnackbar('Podany adres kryptowalutowy nie naley do adnego notariusza w tym systemie.', {variant: 'error'});
      }
      const result = await web3Manager.notaryManagerContract.methods.notaries(notaryPublicKey).call();
      const notaryContractCloned = web3Manager.notaryContract.clone();
      notaryContractCloned.options.address = result; 
      setName(await notaryContractCloned.methods.name().call());
      setSurname(await notaryContractCloned.methods.surname().call());
      setOfficeLocation(await notaryContractCloned.methods.officeLocation().call());
      setNotaryAccountAddress(result);
    } catch (err) { 
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si wczyta danych notariusza: ' + err.message, {variant: 'error'});
    } finally {
      setSubmitInProgress(false);
      setLoading(false);
    }
  }

  const handleUpdateNotary = async (method, value) => {
    try {
      setSubmitInProgress(true);
      setLoading(true); 
      const updateNotaryPromiEvent = web3Manager.updateNotary(notaryAccountAddress, method, value); 
      const miningStatus = await notifyOnMiningStatus(updateNotaryPromiEvent, snackbar, 'Aktualizacja danych notariusza'); 
      console.log('mining status' , miningStatus); 
    } catch (err) { 
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si zaktualizowa danych notariusza: ' + err.message, {variant: 'error'});
    } finally {
      setSubmitInProgress(false);
      setLoading(false);
    }
  }
  return (
    <Grid item xs={6}>
      <Paper className={classes.paper}>
        <Title>
          Edytuj dane notariusza
        </Title>
        <Typography variant="body1">
          W tej sekcji moesz zmieni dane o notariuszu. W tym celu podaj jego adres w polu poniej, a nastpnie wczytaj jego profil.
        </Typography>  
        <div className={classes.textFieldRoot}> 
          <TextField
            id="notaryPublicKey"
            name="notaryPublicKey"
            label="Adres (kryptowalutowy) Notariusza"
            className={classes.textField} 
            margin="normal"
            onChange={handleChange}
            value={notaryPublicKey} 
          />
        </div> 
        <Button variant="contained" color="secondary" onClick={handleLoadNotary} disabled={submitInProgress}>
          Wczytaj
        </Button>
        {notaryAccountAddress && 
          <div>
            <div className={classes.textFieldRoot}> 
              
               
              <Grid container spacing={1} alignItems="flex-end">
                <Grid item style={{flex:1}}>
                  <TextField
                    id="name"
                    name="name"
                    label="Imi"
                    className={classes.textField} 
                    margin="normal"
                    onChange={handleChange}
                    value={name} 
                  />
                </Grid>
                <Grid item>
                  <IconButton onClick = {() => handleUpdateNotary('setName', name)} >
                    <SaveIcon />
                  </IconButton>
                </Grid>
              </Grid> 

              <Grid container spacing={1} alignItems="flex-end">
                <Grid item style={{flex:1}}>
                  <TextField
                    id="surname"
                    name="surname"
                    label="Nazwisko"
                    className={classes.textField} 
                    margin="normal"
                    onChange={handleChange}
                    value={surname} 
                  />  
                </Grid>
                <Grid item>
                  <IconButton onClick = {() => handleUpdateNotary('setSurname', surname)} >
                    <SaveIcon />
                  </IconButton>
                </Grid>
              </Grid> 

              <Grid container spacing={1} alignItems="flex-end">
                <Grid item style={{flex:1}}>
                  <TextField
                    id="officeLocation"
                    name="officeLocation"
                    label="Adres biura (opcjonalne)"
                    className={classes.textField} 
                    margin="normal"
                    onChange={handleChange}
                    value={officeLocation} 
                    />
                </Grid>
                <Grid item>
                  <IconButton onClick = {() => handleUpdateNotary('setOfficeLocation', officeLocation)} >
                    <SaveIcon />
                  </IconButton>
                </Grid>
              </Grid> 
            </div>   
          </div>
        }
      </Paper>
    </Grid>
  );
}
 