import React, { Fragment } from 'react';
import { IconButton, Button } from "@material-ui/core";
import OpenInNewIcon from '@material-ui/icons/OpenInNew';
import CloseIcon from '@material-ui/icons/Close';
import {etherscanBaseUrl} from '../config.json';


const notifyOnMiningStatus = async (promiEvent, snackbar, slug) => {
  let notistackKey = null;
  promiEvent
    .once('transactionHash', function(hash){ 
      notistackKey = snackbar.enqueueSnackbar(`${slug ? `[${slug}] `: ``}Transakcja oczekuje na wykopanie.`,{
        variant: 'info',
        action: (key) => (
          <Fragment>
            <IconButton color="inherit" onClick={() => {
              window.open(`${etherscanBaseUrl}/tx/${hash}`, "_blank"); 
            } }>
              <OpenInNewIcon/>
            </IconButton>
            <IconButton color="inherit" onClick={() => { 
              snackbar.closeSnackbar(key);
            }}>
              <CloseIcon />
            </IconButton>
          </Fragment>
        )
      });
    })
    .once('confirmation', function(confirmationNumber, receipt){ 
      console.log('confirmation', confirmationNumber, receipt);
      snackbar.enqueueSnackbar(`${slug ? `[${slug}] `: ``}Transakcja potwierdzona przez blockchain`, {
        variant: 'success',
        action: (key) => (
          <Fragment>
            <IconButton color="inherit" onClick={() => {
              window.open(`${etherscanBaseUrl}/tx/${receipt.transactionHash}`, "_blank"); 
            } }>
              <OpenInNewIcon/>
            </IconButton>
            <IconButton color="inherit" onClick={() => { 
              snackbar.closeSnackbar(key);
            }}>
              <CloseIcon />
            </IconButton>
          </Fragment>
        )
      });
      snackbar.closeSnackbar(notistackKey); 
    });
  return promiEvent;
}

export default notifyOnMiningStatus;
