import React from 'react';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import DashboardIcon from '@material-ui/icons/Dashboard';
import AddIcon from '@material-ui/icons/Add';
import SettingsIcon from '@material-ui/icons/Settings';
import {Link} from "react-router-dom";
import routes from '../routes.json';
import { Divider, List, Tooltip } from '@material-ui/core';
import {includeNotaryPanel} from '../config.json';
 
export const listMenuItems = (isNotary, isNotaryManager) => (
  <div>

    <List>
     
      <ListItem button component={Link} to={routes.home}>
        <ListItemIcon>
          <DashboardIcon />
        </ListItemIcon>
        <ListItemText primary="Przegldaj dokumenty" />
      </ListItem> 
      {includeNotaryPanel && ( 
        <Tooltip placement="right" title={isNotary?"":"Przecz si na konto notariusza aby uzyska dostp do tej zakadki."}> 
          <div>
            <ListItem button component={Link} to={routes.addDocument} disabled={!isNotary}>
              <ListItemIcon>
                <AddIcon />
              </ListItemIcon>
              <ListItemText primary="Dodaj nowy dokument" />
            </ListItem>
          </div>
        </Tooltip> 
      )}
    </List>
    
    {includeNotaryPanel && <Divider />}
    
    {includeNotaryPanel && ( 
      <List>
        <Tooltip placement="right" title={isNotaryManager?"":"Przecz si na konto administratora systemu aby uzyska dostp do tej zakadki."}> 
          <div>
            <ListItem button component={Link} to={routes.notaryManager}  disabled={!isNotaryManager}>
              <ListItemIcon>
                <SettingsIcon />
              </ListItemIcon>
              <ListItemText primary="Zarzdzaj notariuszami" />
            </ListItem> 
          </div>
        </Tooltip>
      </List> 
    )}
  </div>
);

