import React, { useState } from 'react'; 
import Typography from '@material-ui/core/Typography';
import { Grid, Paper, TextField, Button, IconButton, CircularProgress, Fade } from '@material-ui/core';
import notaryManagerStyles from '../styles/NotaryManager';
import { makeStyles } from '@material-ui/styles';
import SaveIcon from '@material-ui/icons/Save';
import Title from './Title';
import { useSnackbar } from 'notistack'; 
import MaterialTable from 'material-table';
import tableIcons from './tableIcons';
import RefreshIcon from '@material-ui/icons/Refresh';

const useStyles = makeStyles(notaryManagerStyles);

export default function ListAllNotaries(props) { 
  const {web3Manager, setLoading} = props; 
  const classes = useStyles(); 
  const snackbar = useSnackbar();

  const [componentMounted, setComponentMounted] = useState(false);
  const [rows, setRows] = useState([]);

  React.useEffect(() => { 
    web3Manager.listAllNotaries().then((notaries) => {setRows(notaries); setComponentMounted(true);}).catch(() => setComponentMounted(true)); 
  }, []); 
 
  return (
    <Grid item xs={12}>
      <Paper className={classes.paper}>
        <Grid container>
          <Grid item style={{flexGrow: 1}}>

          <Title>
            Lista notariuszy
          </Title>
          </Grid>
          <Grid item>
            <IconButton onClick={() => { 
              web3Manager.listAllNotaries().then((notaries) => {setRows(notaries); setComponentMounted(true);}).catch(() => setComponentMounted(true)); 
            }}> 
              <RefreshIcon />    
            </IconButton>
          </Grid>
        </Grid>
        <Typography variant="body1">
          Poniej lista wszystkich notariuszy nalecych do systemu.
        </Typography>  
        <div className={classes.notariesList}>
          {componentMounted ? (
          
            <MaterialTable
                components={{
                  Container: props => <div {...props}/>
                }}
                columns={[
                  { title: "Adres Notariusza", field: "notaryPublicKey" },
                  { title: "Status", field: "status" },
                  { title: "Block number", field: "block"}
                ]}
                data={rows}
                icons={tableIcons}
                title="Notariusze" 
                // onSelectionChange={handleSelectionChange}
                options={{
                  filtering: false,
                  sorting: true,
                  grouping: false,
                  draggable: false,
                  paging: true,
                  pageSize: 5,
                  pageSizeOptions: [5, 10, 25, 50, 100],
                  selection: false,
                  actionsColumnIndex: -1
                }} 
                localization={{
                  body:{
                    emptyDataSourceMessage: "Brak notariuszy w sytemie."
                  }
                }}
              />
              
          ) : (  
            <Fade in> 
              <Grid container justify="center" alignItems="center" direction="column">
                <Typography gutterBottom className={classes.connectServerText}>
                  Wczytywanie notariuszy...
                  </Typography>
                <CircularProgress />
              </Grid> 
            </Fade>
          )} 
        </div>
      </Paper>
    </Grid>
  );
}
 