import React from 'react';
import Typography from '@material-ui/core/Typography';
import { Grid, Paper, TextField, Button } from '@material-ui/core';
import AddDocumentStyles from '../styles/AddDocument';
import { makeStyles } from '@material-ui/styles';
import Title from './Title';
import { useSnackbar } from 'notistack';
import notifyOnMiningStatus from '../helpers/miningStatusNotifier';
import axios from 'axios';
import config from '../config.json';
import EthCrypto from 'eth-crypto';


const useStyles = makeStyles(AddDocumentStyles);

export default function AddDocumentForm(props) {
  const { web3Manager, setLoading } = props;
  const snackbar = useSnackbar();
  const classes = useStyles();

  const [file, setFile] = React.useState(null);
  const [fileName, setFileName] = React.useState("Upload File");
  const [title, setTitle] = React.useState('');

  const [submitInProgress, setSubmitInProgress] = React.useState(false); 

  const handleChange = event => {
    const inputName = event.target.name;
    if (inputName === 'title') {
      setTitle(event.target.value);
    }
    if (inputName === 'document') {
      if (event.target.files.length > 0) {
        setFile(event.target.files[0])
        setFileName(event.target.files[0].name)
      }
      else {
        setFileName("Upload File")
      }
    }
  };

  const handleSubmit = async () => {
    try {
      setSubmitInProgress(true);
      setLoading(true);
      let formData = new FormData();
      formData.append("file", file);

      const result = await axios.post(`${config.apiUrl}/publishFile`, formData,
        {
          headers: {
            'Content-Type': 'multipart/form-data'
          }
        });
      const ipfsHash = result.data.hash;
      const myEncryptionKey = result.data.key;
      console.log(ipfsHash);
      console.log('myEncryptionKey', myEncryptionKey); 
       
      const myEncryptedKey = await EthCrypto.encryptWithPublicKey(
        web3Manager.getPublicKeyForSelectedAddress(), // encrypt with alice's publicKey
        myEncryptionKey
      );

      const myEncryptedKeyAsString = EthCrypto.cipher.stringify(myEncryptedKey);
      const deploymentPromiEvent = web3Manager.addDocument(title, ipfsHash, myEncryptedKeyAsString);

      const miningStatus = await notifyOnMiningStatus(deploymentPromiEvent, snackbar, 'Nowy dokument');
      console.log('mining status', miningStatus);

    } catch (err) {
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si doda dokumentu: ' + err.message, { variant: 'error' });
    } finally {
      setLoading(false);
      setSubmitInProgress(false);
    }
  }

  return (
    <Grid item xs={12}>
      <Paper className={classes.paper}>
        <Title>
          Dodaj nowy dokument
        </Title>
        <Typography variant="body1">
          Nadaj tytu oraz wybierz plik w formacie PDF.
        </Typography>
        <div className={classes.textFieldRoot}>
          <Grid container spacing={3}>
            <Grid item xs={12}>
              <TextField
                id="title"
                name="title"
                label="Tytu"
                className={classes.textField}
                margin="normal"
                onChange={handleChange}
                value={title}
                inputProps={{autoComplete: "off"}}
              />
            </Grid>
            <Grid item xs={12}>
              <Button
                variant="contained"
                component="label"
                className={classes.uploadButton}
              >
                {fileName}
                <input
                  id="document"
                  name="document"
                  type="file"
                  style={{ display: "none" }}
                  onChange={handleChange} 
                />
              </Button>
            </Grid>
          </Grid>
        </div>
        <Button variant="contained" color="secondary" onClick={handleSubmit} disabled={submitInProgress}>
          Dodaj
        </Button>
      </Paper>
    </Grid>
  );
}
