import React from 'react'; 
import Typography from '@material-ui/core/Typography';
import { Grid, Paper, TextField, Button, Divider } from '@material-ui/core';
import notaryManagerStyles from '../styles/NotaryManager';
import { makeStyles } from '@material-ui/styles';
import Title from './Title';
import { useSnackbar } from 'notistack';
import notifyOnMiningStatus from '../helpers/miningStatusNotifier';

const useStyles = makeStyles(notaryManagerStyles);

export default function AddNotaryForm(props) { 
  const {web3Manager, setLoading} = props; 
  const snackbar = useSnackbar();
  const classes = useStyles();

  const [submitInProgress, setSubmitInProgress] = React.useState(false);
  const [name, setName] = React.useState('');
  const [surname, setSurname] = React.useState('');
  const [publicKey, setPublicKey] = React.useState('');
  const [officeLocation, setOfficeLocation] = React.useState('');
  const [notaryAccount, setNotaryAccount] = React.useState('');
  const handleChange = event => {
    const inputName = event.target.name;
    if(inputName === 'name'){
      setName(event.target.value);
    }
    if(inputName === 'surname'){
      setSurname(event.target.value);
    }
    if(inputName === 'publicKey'){
      setPublicKey(event.target.value);
    }
    if(inputName === 'officeLocation'){
      setOfficeLocation(event.target.value);
    }
    if(inputName === 'notaryAccount'){
      setNotaryAccount(event.target.value);
    }
  };

  const addNotary = async(notaryContractAddress) => {
    const addNotaryPromiEvent = web3Manager.addNotary(notaryContractAddress) 
    const miningStatus = await notifyOnMiningStatus(addNotaryPromiEvent, snackbar, 'Uprawnienia notariusza'); 
    console.log('mining status' , miningStatus); 
  }

  const handleSubmit = async() => { 
    try{ 
      setSubmitInProgress(true);
      setLoading(true);
      const notaryAlreadyExists = await web3Manager.notaryManagerContract.methods.isNotary(publicKey).call();
      if(notaryAlreadyExists){
        throw new Error("Istnieje ju notariusz o adresie kryptowalutowym: " + publicKey);
      }
      const deploymentPromiEvent = web3Manager.deployNotary(name, surname, publicKey, officeLocation); 
      console.log(deploymentPromiEvent);
      const deployedContract = await notifyOnMiningStatus(deploymentPromiEvent, snackbar, 'Zakadanie konta notariusza');  
      console.log("Your contract is under ", deployedContract._address);
      
      //snackbar.enqueueSnackbar(`Utworzono nowe konto notariusza: ${deployedContract._address}`, {variant: 'success'});
      setNotaryAccount(deployedContract._address);
      await addNotary(deployedContract._address)
    }catch(err){
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si doda notariusza: ' + err.message, {variant: 'error'});
    }finally{
      setLoading(false);
      setSubmitInProgress(false)
    } 
  }

  const grantPrivileges = async () => {
    try{ 
      setSubmitInProgress(true);
      setLoading(true); 
      await addNotary(notaryAccount)
    }catch(err){
      console.error(err);
      snackbar.enqueueSnackbar('Nie udao si nada uprawnie dla notariusza: ' + err.message, {variant: 'error'});
    }finally{
      setLoading(false);
      setSubmitInProgress(false)
    } 
  }

  return (
    <Grid item xs={12}>
      <Paper className={classes.paper}>
        <Title>
          Dodaj notariusza
        </Title>
        <Typography variant="body1">
          Aby nada notariuszowi uprawnienia dostpu do systemu naley utworzy dla niego konto. Kady z notariuszy reprezentowany jest przez hash. Hash ten moe zosta pniej wykorzystany do odebrania uprawnie lub zmiany informacji o notariuszu.
        </Typography> 
        <div className={classes.textFieldRoot}> 
          <TextField
            id="name"
            name="name"
            label="Imi"
            className={classes.textField} 
            margin="normal"
            onChange={handleChange}
            value={name} 
          />
          <TextField
            id="surname"
            name="surname"
            label="Nazwisko"
            className={classes.textField} 
            margin="normal"
            onChange={handleChange}
            value={surname} 
          /> 
          <TextField
            id="publicKey"
            name="publicKey"
            label="Adres konta notariusza (Zgoszony przez notariusza - nie ma moliwoci pniejszej zmiany)"
            className={classes.textField} 
            margin="normal"
            onChange={handleChange}
            value={publicKey} 
          />
          <TextField
            id="officeLocation"
            name="officeLocation"
            label="Adres biura (opcjonalne)"
            className={classes.textField} 
            margin="normal"
            onChange={handleChange}
            value={officeLocation} 
          />
        </div>
        <Button variant="contained" color="secondary" onClick={handleSubmit} disabled={submitInProgress}>
          Dodaj
        </Button>

        <Divider className={classes.dividerMargin}/>
        <Typography variant="body1">
          Jeeli notariusz posiada ju zaoone konto, ale nie jest dodany do systemu, moesz nada mu uprawnienia bezporednio podajc hash jego konta w polu poniej.
        </Typography>
        
        <div className={classes.textFieldRoot}> 
          <TextField
            id="notaryAccount"
            name="notaryAccount"
            label="Hash konta notariusza"
            className={classes.textField} 
            margin="normal"
            onChange={handleChange}
            value={notaryAccount} 
          />
        </div>
          
        <Button variant="contained" color="secondary" onClick={grantPrivileges} disabled={submitInProgress}>
          Nadaj uprawnienia
        </Button>
      </Paper>
    </Grid>
  );
}
 