import React from 'react';  
import { Grid, Paper, Fade, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import homeStyles from '../styles/Home';
import FileExplorer from './FileExplorer'; 

const useStyles = makeStyles(homeStyles);

export default function Home(props) {
  const {setLoading, selectedAddress, web3Manager} = props;
  const classes = useStyles();  
  if(!selectedAddress) {
    return (
      <Fade in timeout={1000}> 
        <Paper className={classes.paper}>
          <Grid container justify="center" alignItems="center" direction="column">
            <Typography gutterBottom className={classes.connectServerText}>
              Wymagane podczenie konta poprzez Web3. Sugerujemy wykorzystanie rozszerzenia do przegldarki MetaMask.
            </Typography>
          </Grid>
        </Paper>
      </Fade> 
    )
  }
  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <FileExplorer web3Manager={web3Manager} setLoading={setLoading} selectedAddress={selectedAddress}/>
      </Grid>
    </Grid>
  );
}
 