import React from 'react';
import PropTypes from 'prop-types';
import Typography from '@material-ui/core/Typography';
import { Grid, Paper, TextField, Button } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import axios from 'axios';
import loginStyles from '../styles/Login';
import config from '../config.json';
import { useSnackbar } from 'notistack';

const useStyles = makeStyles(loginStyles);

export default function Login(props) {
  
  const snackbar = useSnackbar();
  const [password, setPassword] = React.useState('');
  const [loginInProgress, setLoginInProgress] = React.useState(false);
  const classes = useStyles(); 
  const handleChange = event => {
    setPassword(event.target.value);
  };
  const handleLogin = async () => {
    let result;
    try{
      props.setLoading(true);
      setLoginInProgress(true);
      result = await axios.post(`${config.apiUrl}/login`, {login: 'admin', password}); 
    } catch(err) { 
      if(err.response && err.response.data && err.response.data.message){
        return snackbar.enqueueSnackbar(err.response.data.message, {variant:'error'});
      }
      snackbar.enqueueSnackbar(`Wystpi bd podczas logowania: ${err.message}`, {variant:'error'});
    } finally{
      setLoginInProgress(false);
      props.setLoading(false);
    }
    if(result && result.data && result.data.success){
      props.setLoggedIn(true);
    }
  }

  
  React.useEffect(() => {
    if(!config.includeNotaryPanel){
      console.log("Do not include Notary Panel - omitting Login page")
      props.setLoggedIn(true);
    }
  }, []);

  
  return (
    <Grid container spacing={3}>
      <Grid item xs={12}>
        <Paper className={classes.paper}>
          <Typography variant="h6" color="primary" gutterBottom>
            Zaloguj si do systemu
          </Typography>
          <Typography variant="body1">
            Aby uzyska dostp do panelu notariusza naley poda haso ustawione podczas uruchamiania serwera.
          </Typography>
          <div className={classes.textFieldRoot}> 
            <TextField
              onKeyPress={(ev) => { 
                if (ev.key === 'Enter') { 
                  ev.preventDefault();
                  handleLogin()
                }
              }}
              id="server-password"
              label="Haso"
              className={classes.textField}
              type="password"
              autoComplete="current-password"
              margin="normal"
              onChange={handleChange}
              value={password}
              autoFocus
            />
          </div>
          <Button variant="contained" color="primary" onClick={handleLogin} disabled={loginInProgress}>
            Zaloguj
          </Button>
        </Paper>
      </Grid>
    </Grid>
    
  );
}

Login.propTypes = {
  setLoggedIn: PropTypes.func.isRequired,
};
