import React from 'react';
import Typography from '@material-ui/core/Typography';
import { Grid, Paper, TextField, Button, Table, TableHead, TableRow, TableCell, TableBody } from '@material-ui/core'; 
import { makeStyles } from '@material-ui/core/styles'; 
import Title from './Title';
import { useSnackbar } from 'notistack';
import notifyOnMiningStatus from '../helpers/miningStatusNotifier';
import axios from 'axios';
import config from '../config.json';  
 
const useStyles = makeStyles(theme => ({
  pulse: {
    opacity: 0.7
  }   
}));

export default function DocumentInformations(props) {
  const { document } = props;
  const classes = useStyles();
 
  return (
    <Grid container> 
      <Grid item xs={12}> 
        <Table size="small">
          <TableHead>
            <TableRow>
              <TableCell style={{ width: '30%' }}>Parametr</TableCell>
              <TableCell>Warto</TableCell> 
            </TableRow>
          </TableHead>
          <TableBody> 
            <TableRow >                      
              <TableCell>Nazwa</TableCell>
              <TableCell >
                {document.name}
              </TableCell>
            </TableRow> 

            <TableRow >                      
              <TableCell>Hash IPFS</TableCell>
              <TableCell >
                {document.link}  
              </TableCell>
            </TableRow> 

            <TableRow >                      
              <TableCell>Data utworzenia</TableCell>
              <TableCell >
                {document.date}  
              </TableCell>
            </TableRow> 
            
            <TableRow >  
              <TableCell>Kryptowalutowy adres notariusza</TableCell>
              <TableCell>
                {document.notaryPublishedBy}  
              </TableCell>
            </TableRow> 

            <TableRow >  
              <TableCell>Imi notariusza</TableCell>
              <TableCell >
                {document.notaryName === undefined && (
                  <div className={classes.pulse}>
                    Wczytywanie...
                  </div>
                )}  
                {document.notaryName}
              </TableCell>
            </TableRow> 

            <TableRow >  
              <TableCell>Nazwisko notariusza</TableCell>
              <TableCell >
                {document.notarySurname === undefined && (
                  <div className={classes.pulse}>
                    Wczytywanie...
                  </div>
                )}  
                {document.notarySurname}  
              </TableCell>
            </TableRow> 

            <TableRow >  
              <TableCell>Biuro notariusza</TableCell>
              <TableCell >
                {document.notaryOfficeLocation === undefined && (
                  <div className={classes.pulse}>
                    Wczytywanie...
                  </div>
                )} 
                {document.notaryOfficeLocation}  
              </TableCell>
            </TableRow> 


          </TableBody>
        </Table> 
      </Grid>
    </Grid>
  );
}
